<template>
  <div>
    <Tabs
      :dataSource="tabs"
      :defaultTab="defaultTab"
      @onClick="handleClick"
    ></Tabs>
    <router-view></router-view>
    <ADs class="flex lg:hidden flex-col items-center" />
  </div>
</template>

<script>
import Tabs from "@/components/Tabs";
import ADs from "@/components/ADs";
export default {
  components: {
    Tabs,
    ADs,
  },
  data() {
    return {
      defaultTab: "",
    };
  },
  computed: {
    tabs() {
      return [
        {
          key: "policy",
          name: this.$t("system_policy"),
        },
        {
          key: "service",
          name: this.$t("system_service"),
        },
        {
          key: "privacy",
          name: this.$t("system_privacy"),
        },
        {
          key: "disclaimer",
          name: this.$t("system_disclaimer"),
        },
      ];
    },
  },
  methods: {
    handleClick(key) {
      this.$router.push(`/system/${key}`);
    },
  },
  watch: {
    "$route.path": {
      immediate: true,
      handler(value) {
        this.defaultTab = value.replace("/system/", "");
      },
    },
  },
};
</script>

<style></style>
